*{box-sizing:border-box}body{font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#f15946;transition:0.4s ease-in-out}html,body,#root{width:100%;height:100%;margin:0;padding:0;overflow:hidden}.scrollArea{position:absolute;top:0;left:0;width:100vw;height:100vh;overflow:auto;overflow-x:hidden}.container{margin:0 auto;width:100%;max-width:100%;height:100%;display:flex;align-items:center;justify-content:center}header{position:absolute;width:100%;padding:30px 60px 30px 60px}header .header-inner{display:flex;align-items:center;justify-content:space-between}header .header-inner .logo{font-weight:900;letter-spacing:2px;color:#fff}header .header-inner nav ul{margin:0;padding:0;display:flex}header .header-inner nav ul li{list-style:none;margin:0 60px;z-index:99}header .header-inner nav ul li.btn p{color:#fff;font-weight:600;background:#23232a;padding:16px 24px;border-radius:10px}header .header-inner nav ul li p{text-transform:capitalize;text-decoration:none;color:#fff}header .header-inner nav ul li:hover{cursor:pointer}.title{font-size:10rem;text-align:center;width:100%;margin:0 auto;color:#fff}.title span{display:block}@media (max-width: 1440px){.title{font-size:6rem}}.loading{position:absolute;top:0px;left:0px;width:100%;height:100%;background:#171717;display:flex;align-items:center;justify-content:center;z-index:1000}.loading-bar-container{width:100px;height:32px;background:#272727}.loading-bar{height:32px;background:#f15946}.scrollArea::-webkit-scrollbar-track{box-shadow:inset 0 0 6px rgba(0,0,0,0.3);border-radius:10px;background-color:#F5F5F5}.scrollArea::-webkit-scrollbar{width:12px;background-color:#F5F5F5}.scrollArea::-webkit-scrollbar-thumb{border-radius:10px;box-shadow:inset 0 0 6px rgba(0,0,0,0.3);background-color:#555}

